/* CARD */

.borderCard {
  border-radius: 20px;
  background: linear-gradient(170deg, rgba(58, 56, 56, 0.623) 0%, rgb(31, 31, 31) 100%);
  box-shadow: 0 25px 50px rgba(0,0,0,0.55);
}

.borderCardWhite {
  border-radius: 50px;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe,
              -20px -20px 60px #ffffff;
}

.border-radius {
  border-radius: 10px;
}

.bg-ligth {
  background-color: #F0EAF9;

}



