/* BUTTON */

.myBtn {
  position: relative;
  display: inline-block;
  /* margin: 15px; */
  padding: 5px 10px;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
  text-decoration: none;
  color: #8D50FF;
  background: transparent;
  cursor: pointer;
  transition: ease-out 0.8s;
  border: 2px solid #8D50FF;
  border-radius: 10px;
  box-shadow: inset 0 0 0 0 #8D50FF;
} .myBtn:hover {
  color: white;
  box-shadow: inset 0 -100px 0 0 #8D50FF;
} .myBtn.active {
  color: white;
  background-color:#8D50FF;
}
  
  
